/*
 *  Article
 */

 #showfile {
    font-size: large;
    margin-left: 2vw;
  }
  
  #showfile h1,
  #showfile h2,
  #showfile h3,
  #showfile h4,
  #showfile h5,
  #showfile h6{
    margin: 2vw;
  }
  #showfile h1,
  #showfile h2{
    border-bottom: #dfe2e5 solid 1px;
  }
  
  #showfile img{
     display: block;
     width : auto;
     max-width: 100%;
     padding-left:2vw;
   }
  
  #showfile code{
     background:#dfe2e5;
     color: black;
     padding:3px;
     
     border-radius: 3px;
   }
  
   #showfile blockquote{
    border-left: #dfe2e5 solid 4px;
    padding-left: 2rem;
   }
   
   #showfile p,
   #showfile ul,
   #showfile ol ,
   #showfile blockquote{
    margin-left:2vw;
   }
  