#navibar {
    list-style-type: none;
    margin: 0;
    padding: 12 20;
    overflow: hidden;
    background-color: #feffff;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
  }
  
  .naviitem {
    float: left;
  }
  
  .naviitem a ,.naviitem div{
    display: block;
    color: black;
    text-align: center;
    padding: 7px 16px;
    text-decoration: none;
  }
  
  .naviitem a:hover:not(.active),
  .naviitem div:hover:not(.active)
  {
    cursor: pointer;
    color: gray;
  }
  .manubutton span{
    width: 20px;
    height: 2px;
    background-color: #222;
    display: block;
    margin: 6px auto;
  }

  #manu{
    z-index: 150;
    position: fixed;
    top:0;
    width: 100%;
    background-color: var(--logoColor);
    transition:all 0.5s linear;
  }
  .manuhidden{
    visibility: hidden;
    opacity:0;
    height: 0;
  }
  .manudissplay{
    visibility: visible;
    opacity:1;
    height: 100%;
  }
  .manuclosebtn{
    margin: 0;
    padding: 14px 20px;
    float: right;
    font-size: x-large;
  }

 
  .logoCont img{
    height: 30px;
  }
  .logoCont span{
    margin-left: 8px;
    position: fixed;
  }
  .logoY{
    color: #5f84a2;
    font-weight: bold;
  }