

.Recommendation {
    display: grid;
    grid-template-columns: 60% 40%;
    margin: 48px 10vw 48px;
}


.rcmone {
    grid-column-start: 1;
    grid-row-start: 1;
    grid-row-end: 3;
}



@media (max-width: 600px) {
    .Recommendation {
        display:inline-block;
    }
}


.Recommendation img {
    width: 100%;
}