.wholepage {
  height: 100vh;
  display: flex;
  align-items: center;
  
}

.slideshow {
  margin: auto;
  height:80vw;

  max-height: 80vh;
  max-width:90vw;

  object-fit: cover;
  clip-path: polygon(10.42% 5.25%,65.42% 0%,100% 23.38%,89.58% 90.75%,35.67% 100%, 4.17% 83.63%,0% 48.75%);
  overflow: hidden;
}

.slideshow img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity 1s ease;

}

.fade-in {
  opacity: 1;
}

.fade-out {
  opacity: 0;
}
.slideshowborder{
  position: absolute;
  display: inline;
  height:80vw;
  max-height: 80vh;
  width: 100vw;
}
.slideshowbordersvg{
  height:80vw;
  max-height: 80vh;
  width: 100vw;
  
}

.slidshowTitle{
  position: absolute;
  font-size: xx-large;
  background-color: rgba(255,255,255,0.2);
  top:40vh;
  left:75vw;
  writing-mode: vertical-rl
}


.slidshowSubTitle{
  position: absolute;
  background-color: rgba(255,255,255,0.2);
  top:90vh;
  left:20vw;
  width: 50vw;
  min-width: 15em;
  text-align: left;
  text-indent: 1em;
}

@media screen and (max-width: 1200px) {
  .slidshowSubTitle{
    top:85vh;
  }
  .slidshowTitle{
    top:10vh;
    left:85vw;
  }
}

@media screen and (max-width: 600px) {
  .slidshowSubTitle{
    top:75vh;
  }
  .slidshowTitle{
    top:18vh;
    left:15vw;
    writing-mode: horizontal-tb;
  }
}
