.Fourclass {
    height: 80vh;
    margin: 24px 0px 24px;
    padding: 12px 5vw 12px;
    background-color:var(--bgColorMenu) ;
    /*border: #E8e8e8 3px solid;
    border-radius: 4px;*/
}

.menu {

    margin: 0;
    display: flex;
    /* Works well with 100% width  */

    font-size: 16px;
    padding: 0 10em;
    position: relative;
    align-items: center;
    justify-content: center;


}

.menu__item {

    all: unset;
    flex-grow: 1;
    z-index: 50;
    display: flex;
    cursor: pointer;
    position: relative;
    border-radius: 50%;
    align-items: center;
    will-change: transform;
    justify-content: center;
    padding: 0.55em 0 0.85em;
    transition: transform var(--timeOut, var(--duration));

}

.menu__item::before {

    content: "";
    z-index: -1;
    width: 4.2em;
    height: 4.2em;
    border-radius: 50%;
    position: absolute;
    transform: scale(0);
    transition: background-color var(--duration), transform var(--duration);

}


.menu__item.active {

    transform: translate3d(0, -.8em, 0);

}

.menu__item.active::before {

    transform: scale(1);

}

.icon {

    width: 2.6em;
    height: 2.6em;
    stroke: #2e2e2e;
    fill: transparent;
    stroke-width: 0.3pt;
    stroke-miterlimit: 10;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-dasharray: 400;

}

.menu__item.active .icon {

    animation: strok 1.5s reverse;

}

@keyframes strok {

    100% {

        stroke-dashoffset: 400;
    }
}


@media screen and (max-width: 600px) {
    .menu {
        font-size: 12px;
        padding: 0 5em;
    }
}